import React, { Component } from 'react';
import { MemoryRouter } from 'react-router-dom';
import Container from 'react-bootstrap/Container';
import Jumbotron from 'react-bootstrap/Jumbotron';

import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

import './App.css';

import { Switch, Route } from 'react-router-dom';

import { ToolTipNavButtons } from './view/ToolTipNavButtons'

import { Home } from './view/Home'

import Catalogue from './view/Catalogue'


import { TourPackage } from './view/TourPackage'

import { FreeAndEasy } from './view/FreeAndEasy'

import { FixNavBar } from './view/FixNavBar'

import { Membership } from './view/Membership'

import { ContactUs } from './view/ContactUs'

import ProductCard from './view/Product'

import CommonProvider from './contexts/common';

import ProductsProvider from './contexts/products';

import CartProvider from './contexts/cart';

const BackgroundStyle = {

    Jumbotron: {
        backgroundSize: '100% 100%',
        backgroundRepeat: 'no-repeat',
        backgroundPosition: 'center top',
    }
};


class App extends Component {
    constructor(props) {
        super(props);

        this.state = {
            jumbotronImg: {},
            headerStyle: { color: 'white', textAlign: "left" },
            headerText: "Welcome To Terry Tour Agency"
        };
    }

    setJumbotronStyle = (newStyle) => {
        const { newJumbotronImg, newHeaderStyle, newHeaderText } = newStyle

        this.setJumbotronImg(newJumbotronImg)
        this.setHeaderStyle(newHeaderStyle)
        this.setHeaderText(newHeaderText)
    }

    setJumbotronImg = (newJumbotronImg) => {

        var backgroundStyle = {
            ...BackgroundStyle.Jumbotron,
            ...newJumbotronImg
        };

        this.setState({ jumbotronImg: backgroundStyle })
    }

    setHeaderStyle = (newHeaderStyle) => {
        this.setState({ headerStyle: newHeaderStyle })
    }


    setHeaderText = (newHeaderText) => {
        this.setState({ headerText: newHeaderText })
    }


    render() {

        return (
            <CommonProvider>
                <ProductsProvider>
                    <CartProvider>
                        <MemoryRouter>
                            <Container fluid="sm" className="body" >
                                <FixNavBar />
                                <Jumbotron id="Jumbotron" style={this.state.jumbotronImg}>
                                    <div style={this.state.headerStyle}>
                                        <h1>{this.state.headerText}</h1>
                                    </div>
                                    <p></p>

                                    <Row>
                                        <Col lg={{ span: 4, offset: 4 }} style={{ marginTop: "20px" }}>
                                            <div className="justify-content-end">
                                                <ToolTipNavButtons />
                                            </div>
                                        </Col>
                                    </Row>
                                </Jumbotron>
                                <Switch>
                                    <Route exact path='/'
                                        render={props =>
                                            <Catalogue />} />

                                    <Route path='/tourPackage/:destination'
                                        render={props =>
                                            <TourPackage
                                                {...props}
                                                loadJumbotronStyle={this.setJumbotronStyle}
                                            />} />

                                    <Route path='/freeAndEasy'
                                        render={props =>
                                            <FreeAndEasy
                                                loadJumbotronStyle={this.setJumbotronStyle} />} />

                                    <Route path='/memberShip'
                                        render={props =>
                                            <Membership
                                                loadJumbotronStyle={this.setJumbotronStyle} />} />

                                    <Route path='/Contact Us'
                                        render={props =>
                                            <ContactUs
                                                loadJumbotronStyle={this.setJumbotronStyle} />} />

                                    <Route path='/Product'
                                        render={props =>
                                            <ProductCard />} />
                                </Switch>
                            </Container>
                        </MemoryRouter>
                    </CartProvider>
                </ProductsProvider>
            </CommonProvider>
        );
    }
}

export default App;
